@use "styles/stylesFunctions.scss" as stylesFunctions;

.body {
  background-color: #f8f8f8;
}
.accordionItem {
  padding-top: 75px;
  padding-bottom: 150px;
}

.accordionDefualtItem {
  border: none;
  background-color: transparent;
}

.orderContainer {
  row-gap: 100px;
  justify-content: center;
  .headers {
    @include stylesFunctions.flexLayout(center, center, column, 20px);
    h3,
    p {
      margin: 0;
      text-align: center;
    }
    h3 {
      @include stylesFunctions.fontStyles(40px, 35px, 600, #000000, #000);
    }
    p {
      @include stylesFunctions.fontStyles(20px, 18px, 300, #000000, #000);
    }
  }
  .form {
    background-color: white;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    opacity: 1;
    @include stylesFunctions.paddingY(80px);
    @include stylesFunctions.flexLayout(center, flex-start, column, 50px);
    @include stylesFunctions.lg() {
      padding-inline: 17%;
    }

    .selectInput {
      width: 100%;
      padding: 13px 20px;
      @include stylesFunctions.flexLayout(center, flex-start, column, 17px);
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 0.5px solid rgba(0, 0, 0, 0.1607843137);
      border-radius: 5px;
      opacity: 1;
      label {
        @include stylesFunctions.fontStyles(28px, 21px, 300, #000000, #000);
        opacity: 0.5;
      }
      select {
        width: 100%;
        @include stylesFunctions.fontStyles(39px, 29px, 500, #000000, #000);
      }
    }

    .addField {
      width: 80%;
      align-self: center;
      justify-self: center;
      border: 0.5px solid #00000029;
      border-radius: 5px;
      opacity: 1;
      cursor: pointer;
      @include stylesFunctions.flexLayout(center, center, row, 50px);
      @include stylesFunctions.fontStyles(28px, 21px, 500, #ffaa03, #000);
      @include stylesFunctions.paddingY(15px);
      @include stylesFunctions.lg() {
        width: 50%;
      }
    }

    .submiteBtn {
      width: 100%;
      button {
        width: 100%;
        background: #001736 0% 0% no-repeat padding-box;
        border-radius: 5px;
        opacity: 1;
        @include stylesFunctions.paddingY(20px);
        @include stylesFunctions.fontStyles(20px, 18px, 600, #000000, #000);
        color: white;
      }
    }

    .checkBox {
      label {
        @include stylesFunctions.flexLayout(flex-start, center, row, 10px);
        span {
          @include stylesFunctions.fontStyles(28px, 21px, 500, #000000, #000);
          a {
            text-decoration: underline;
          }
        }
      }
    }
    .addFieldWithLabel {
      width: 100%;
      @include stylesFunctions.flexLayout(initial, center, row, initial);
      p {
        margin: 0;
        @include stylesFunctions.fontStyles(54px, 40px, 300, #000000, #000);
        flex: 15;
      }
      input[type="button"],
      input[type="checkbox"] {
        display: none;
        & ~ span {
          &::after,
          &::before {
            content: " ";
            position: absolute;
            top: 50%;
            left: 50%;
            width: 60%;
            height: 1.5px;
            background-color: black;
            transition: all ease-in-out 300ms;
          }
          &::before {
            transform: translate(-50%, -50%) rotate(90deg);
          }
          &::after {
            transform: translate(-50%, -50%);
          }
        }
        &:checked ~ span {
          background-color: #ffaa03;

          &::after,
          &::before {
            background-color: white;
          }
          &::before {
            transform: translate(-75%, 0px) rotate(110deg) scaleY(1) scaleX(0.5);
          }
          &::after {
            transform: translate(-45%, 0px) rotate(145deg);
          }
        }
      }

      span {
        position: relative;
        flex: 1;
        aspect-ratio: 1;
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 0.5px solid #00000041;
        border-radius: 10px;
        opacity: 1;
        @include stylesFunctions.flexLayout(center, center, row, initial);
      }
    }
  }
}

.hideTable {
  height: 0;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-1000px);
  transition: all ease-out 350ms;
}

.showTable {
  height: fit-content;
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
  transition: all ease-in 400ms;
}

.tableBody {
  @include stylesFunctions.paddingY(75px);
  padding-bottom: 0;
  row-gap: 80px;
  h2 {
    @include stylesFunctions.fontStyles(45px, 40px, 600, #000000, #000);
  }
  .tableContainer {
    background: #ffffff 0% 0% no-repeat padding-box;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
    opacity: 1;
    padding: 55px 65px;
    .tableContainerHeader {
      margin-bottom: 40px;
      @include stylesFunctions.flexLayout(flex-start, center, row, 10px);
      svg {
        transform: scale(1.3);
      }
      h3 {
        margin: 0;
        @include stylesFunctions.fontStyles(24px, 18px, 600, #001736, #000);
      }
    }
    .moreInfo {
      margin-top: 40px;
      row-gap: 25px;
      label {
        padding: 16px 18px;
        @include stylesFunctions.flexLayout(flex-start, center, row, 16px);
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 0.5px solid #00000041;
        border-radius: 5px;
        opacity: 1;
        input {
          @include stylesFunctions.fontStyles(24px, 18px, 500, #001736, #000);
        }
      }
      p {
        @include stylesFunctions.fontStyles(42px, 32px, 500, #001736, #000);
        margin: 0;
        span {
          opacity: 0.5;
        }
      }
      button {
        padding: 9px 30px;
        background: #ffaa03 0% 0% no-repeat padding-box;
        border-radius: 10px;
        opacity: 1;
        @include stylesFunctions.fontStyles(40px, 24px, 300, #ffffff, #000);
      }
    }
  }
}

.deleteBtn {
  background-color: transparent;
}

.customerInfoBody {
  justify-content: center;
  width: 100%;
  align-self: center;
  .customerInfoContainer {
    width: 100%;
    @include stylesFunctions.flexLayout(center, center, column, 40px);
    .submiteBtn {
      width: 100%;
      button {
        @include stylesFunctions.flexLayout(center, center, row, 10px);
        width: 100%;
        background: #001736 0% 0% no-repeat padding-box;
        border-radius: 5px;
        opacity: 1;
        @include stylesFunctions.paddingY(20px);
        @include stylesFunctions.fontStyles(20px, 18px, 600, #000000, #000);
        color: white;
        box-shadow: 0px 3px 6px #00000029;
        border-radius: 10px;
        svg {
          fill: currentColor;
        }
      }
    }
    .formInput {
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 0.5px solid #0017363e;
      border-radius: 5px;
      opacity: 1;
      width: 100%;
      padding: 13px 25px;
      @include stylesFunctions.flexLayout(center, flex-start, column, 10px);
      label {
      }
      input {
        @include stylesFunctions.fontStyles(29px, 22px, 500, #000000, #000);
      }
    }
  }
}
